.footer {
  background: $color-footer;
  border-top: 1px solid $color-border;
  padding: 10px 0;



  .menu {
    column-count: 2;
    padding: 0;

    li {
      list-style: none;
      a {
        color: $color-font;
        text-decoration: none;
        transition: 0.3s ease;

        &:hover {
          color: $color-cart;
          margin-left: 2px;
        }
      }
    }
  }
}

.footer__content {
  padding: 0 15px;
  text-align: center;
  @include tablet {
    display: flex;
    justify-content: space-between;


    .region {
      box-sizing: border-box;
      @include tablet {
        width: 33%;
      }
    }
  }

}


.region-footer {
  text-align: center;
}
.region-footer-right {
  text-align: right;
}

