.s-social__link {
  display: inline-block;
  background-image: url(img/sprite.png);
  width: 40px;
  height: 40px;
  margin: 5px;
  cursor: pointer;
  font-size: 0;
  outline: 0;
  opacity: 0.8;
  transition: opacity 0.3s ease;

  &:hover {
    opacity: 1;
  }

  &--fb {
    background-position: -96px -81px;
  }
  &--gp {
    background-position: -126px -133px;
  }
  &--tw {
    background-position: -42px -133px;
  }
  &--in {
    background-position: -84px -133px;
  }
  &--pi {
    background-position: 0px -133px;
  }
}

