.header__content,
.page,
.footer__content,

.copyright {
  margin: 0 auto;
  @include desktop {
    max-width: 1600px;
  }
}

.main-content {
  font-size: 0;
  padding: 0 15px;

}

.region-front-popular {
  font-size: $font-size-base;
  @include tablet {
    display: inline-block;
    vertical-align: top;
    width: 75%;
  }


}

.region-front-deal {
  font-size: $font-size-base;
  @include tablet {
  display: inline-block;
  vertical-align: top;
  width: 25%;

  }
}

.region-footer-left,
.region-footer,
.region-footer-right {
  display: inline-block;
  vertical-align: top;

  @include tablet {
    width: 33.33%;
  }
}