
.mf-product {
  display: block;
  font-size: 16px;
  font-family: $font-family-header;
  color: $color-font;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #959595;
  text-align: left;



  @include tablet {
    width: 48%;
  }


  @include desktop {

    width: calc(1/3*100% - (1 - 1/3)*20px);
  }

  @include x-wide {
    width: calc(1/4*100% - (1 - 1/4)*20px);
  }


}

.mf-product__image {
  img {
    width: 100%;
    height: auto;
  }
}

.mf-product__title {
  a {
    color: inherit;
    text-decoration: none;
    font-size: 0.75em;
    display: inline-block;
    margin: 10px 0;
  }
}

.mf-product__price {
  font-weight: bold;
  display: inline-block;
  vertical-align: top;
}

.mf-product__cart {
  display: inline-block;
  vertical-align: top;
  float: right;
  position: relative;

  &::after {
    content: '';
    display: block;
    position: absolute;
    right: 7px;
    top: 0;
    background-image: url(img/sprite.png);
    background-position: -210px -133px;
    width: 22px;
    height: 18px;
    pointer-events: none;
  }

  input[type=submit] {
    display: inline-block;
    background: none;
    border: 0;
    padding-right: 40px;
    color: $color-cart;
    font-size: 0.875em;
    cursor: pointer;
    line-height: 1;
    height: auto;
  }
}

